  $primary-color: #007bff;
  $primary-hover: #0056b3;
  $bg-color: rgb(255, 255, 255);
  $border-radius: 20px;
  $shadow: 0 4px 10px rgba(0, 0, 0, 0.1);

  .numerlogyChart {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
  }


  .shema {
    position: relative;
    display: flex;
    justify-content: center;
    gap: 15px;
    padding: 20px;
    background: $bg-color;
    border-radius: $border-radius;
    box-shadow: $shadow;
    width: 530px;

    img {
     width: 100%;
    }

    .circle {
      position: absolute;
      width: fit-content;
      border-radius: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .number{
      position: absolute;
       display: flex;
      align-items: center;
      justify-content: center;
      font-size: 12px;
    }
  }

  .calculate-button {
    position: absolute;
    width: 130px;
    height: 40px;
    font-size: 16px;
    color: white;
    background: $primary-color;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    transition: 0.3s;
    bottom: 65px;
    &:hover {
      background: $primary-hover;
    }
    
  }

  // Размеры кругов
  .circle {

    &.small {
      height: 20px;
      width: 20px;
      font-size: 14px;
      padding-bottom: 2px;
    }

    &.medium {
      height: 25px;
      width: 25px;
      font-size: 15px;
    }

    &.mediumPlus{
      height: 29px;
      width: 29px;
      font-size: 16px;
    }

    &.large {
      height: 35px;
      width: 35px;
      font-size: 18px;
    }

    &.outlined {
      box-shadow:  0 0 0 1px black;
    }
  }

  // Позиционирование элементов
  @mixin circle-position($top, $left: auto, $right: auto, $bg-color: white) {
    top: $top;
    left: $left;
    right: $right;
    background-color: $bg-color;
  }
  @mixin number-position($top, $left: auto,$right:auto){
  top:$top;
  left: $left;
  right: $right; 
  }

  // Верхние элементы
  .top1 { @include circle-position(7%, 46.4%, auto,#a41c66); }
  .top2 { @include circle-position(15%, 47.3%, auto, #01aa9e); }
  .top3 { @include circle-position(25%, 47.8%, auto, #03b4e8); }
  .top4 { @include circle-position(37%, 47.8%, auto,  #90c543); }

  // Верхний правый угол
  .topRight1 { @include circle-position(18%, auto, 18.3%); }
  .topRight2 { @include circle-position(25%, auto, 25.6%); }
  .topRight3 { @include circle-position(30%, auto, 30.6%); }

  // Правая сторона
  .right1 { @include circle-position(46.4%, auto, 8%, #f14036) }
  .right2 { @include circle-position(47.6%, auto, 15.6%); }
